<template>
    <div class="drip">
        <div class="drop"/>
        <div class="wave-box">
            <div class="wave"/>
        </div>
    </div>
</template>

<script>
    export default {}
</script>

<style lang="scss">
    $time: 5s;
    $dropW: 0.8rem;
    $waveW: 90 * $dropW;
    $waveH: 0.3 * $waveW;
    $waveInsideW: 0.2 * $waveW;
    $waveInsideH: 0.2 * $waveH;
    $dripC: white;
    $dropStartTop: 50%;
    $dropEndTop: 80%;

    .drip {
        .wave-box {
            @include align(24);
        }
        .wave-box {
            @include wh(100%, 60%);
            @include align(13);
        }
        .drop {
            opacity: 0;
            top: $dropStartTop;
            position: absolute;
            margin-top: -8rem;
            background-color: $dripC;
            @include align(24);
            @include wh($dropW, $dropW);
            @include radius($dropW);
            @include animation-name(drip);
            @include animation-time-function(1,0,.91,.19);
            @include animation-duration($time);
            @include animation-iteration-count;
        }
        .drop:before {
            content: "";
            top: -0.8 * $dropW;
            position: absolute;
            @include wh(0, 0);
            @include border-lr(0.4 * $dropW solid transparent);
            border-bottom: $dropW solid $dripC;
        }
        @keyframes drip {
            0% {
                top: $dropStartTop;
            }
            70% {
                opacity: 1;
                top: $dropStartTop;
            }
            100% {
                top: $dropEndTop;
            }
        }
        .wave {
            opacity: 0;
            overflow: hidden;
            border: $dripC 0.3rem solid;
            @include align;
            @include circle(0.1rem);
            @include animation-name(ripple);
            @include animation-duration($time);
            @include animation-delay($time);
            @include animation-iteration-count;
        }
        .wave:before {
            content: "";
            opacity: 0;
            border: $dripC 0.2rem solid;
            @include circle(0.1rem);
            @include animation-delay($time);
            @include animation-name(ripple2);
            @include animation-duration($time);
            @include animation-iteration-count;
        }
        @keyframes ripple {
            0% {
                opacity: 1;
                border: $dripC 0.3rem solid;
            }
            40%,to {
                opacity: 0;
                border-width: 1px;
                @include wh($waveW, $waveH);
                @include radius-wh(0.5 * $waveW, 0.5 * $waveH);
            }
        }
        @keyframes ripple2 {
            0% {
                opacity: 1;
            }
            40%,to {
                opacity: 0;
                border-width: 1px;
                @include wh($waveInsideW, $waveInsideH);
                @include radius-wh(0.5 * $waveInsideW, 0.5 * $waveInsideH);
            }
        }
    }
</style>
