<template>
    <div id="home">
        <navigation class="nav"/>
        <material class="cube cube-external" width="5rem" height="5rem" thick="5rem" color="rgba(255,255,255,.5)" border-color="rgba(255,255,255,0.2)"/>
        <material class="cube cube-inner" width="3rem" height="3rem" thick="3rem" color="rgba(255,255,255,.5)" border-color="(255,255,255,0.2)"/>
        <drip/>
        <div id="filing">粤ICP备19072727号</div>
    </div>
</template>

<script>
    import req from "@/utils/req";
    import api from "@/config/api";
    import drip from "./drip";
    import navigation from "@/components/navigation_bar";
    import material from "@/components/material";

    export default {
        data() {
            return {

            }
        },
        components: {
            navigation: navigation,
            drip: drip,
            material: material
        },
        beforeRouteEnter(to, from, next) {
            window.document.body.classList.add("home-background");
            next();
        },
        beforeRouteLeave(to, from, next) {
            window.document.body.classList.remove("home-background");
            next();
        }
    }
</script>

<style lang="scss">
    .home-background {
        background-image: none;
        background-color: #2B2B2B;
    }
    #home {
        @include align(24, $column);
    }
    #home {
        .drip {
            overflow: hidden;
            @include wh(100%, icalc(100vh, $navH));
            @include align(3, $column);
        }
        .cube {
            position: absolute;
            margin-top: -8rem;
            top: 50%;
            left: 50%;
            @include transform(rotateX(-36deg) rotateY(45deg));
            @include transform-style(preserve-3d);
        }
        .cube-external {
            @include animation(clockwise 5s ease-in-out infinite 2s);
        }
        .cube-inner {
            @include animation(counterclockwise 5s ease-in-out infinite 2s);
        }
        @keyframes clockwise {
            0% {
                @include transform(rotateX(-36deg) rotateY(45deg));
            }
            40%, to {
                @include transform(rotateX(-36deg) rotateY(-315deg));
            }
        }
        @keyframes counterclockwise {
            0% {
                @include transform(rotateX(-36deg) rotateY(45deg));
            }
            40%, to {
                @include transform(rotateX(-36deg) rotateY(315deg));
            }
        }
        #filing {
            position: absolute;
            right: 1rem;
            bottom: 0.5rem;
            @include font(0.8rem, white);
        }
    }
</style>
