<template>
    <div id="material-box">
        <div class="material">
            <div class="top" :style="topStyle">
                <slot name="top"/>
            </div>
            <div class="bottom" :style="bottomStyle">
                <slot name="bottom"/>
            </div>
            <div class="left" :style="leftStyle">
                <slot name="left"/>
            </div>
            <div class="right" :style="rightStyle">
                <slot name="right"/>
            </div>
            <div class="front" :style="frontStyle">
                <slot name="front"/>
            </div>
            <div class="back" :style="backStyle">
                <slot name="back"/>
            </div>
        </div>
    </div>
</template>

<script>
    import Style from "@/utils/enery/Style";
    import Unit from "@/utils/enery/Unit";
    import col from "@/utils/col";

    export default {
        data() {
            return {
                topBackColor: this.topColor || this.color,
                bottomBackColor: this.bottomColor || this.color,
                leftBackColor: this.leftColor || this.color,
                rightBackColor: this.rightColor || this.color,
                frontBackColor: this.frontColor || this.color,
                backBackColor: this.backColor || this.color,
            }
        },
        props: {
            width: {
                required: true
            },
            height: {
                required: true
            },
            thick: {
                required: true
            },
            color: {
                default: "#873740"
            },
            borderColor: {
                default: "#A93F49"
            },
            topColor: "",
            bottomColor: "",
            leftColor: "",
            rightColor: "",
            frontColor: "",
            backColor: ""
        },
        computed: {
            topStyle() {
                return new Style().append("width", this.width)
                    .append("height", this.thick)
                    .append("background-color", this.topBackColor)
                    .append("border", "1px solid " + this.borderColor)
                    .append("margin-top", new Unit(-0.5).mult(this.thick).value)
                    .append("margin-left", new Unit(-0.5).mult(this.width).value)
                    .transform("rotateX(90deg) translateZ(" + new Unit(0.5).mult(this.height).value + ")")
                    .value;
            },
            bottomStyle() {
                return new Style().append("width", this.width)
                    .append("height", this.thick)
                    .append("background-color", this.bottomBackColor)
                    .append("border", "1px solid " + this.borderColor)
                    .append("margin-top", new Unit(-0.5).mult(this.thick).value)
                    .append("margin-left", new Unit(-0.5).mult(this.width).value)
                    .transform("rotateX(-90deg) translateZ(" + new Unit(0.5).mult(this.height).value + ")")
                    .value;
            },
            leftStyle() {
                return new Style().append("width", this.thick)
                    .append("height", this.height)
                    .append("background-color", this.leftBackColor)
                    .append("border", "1px solid " + this.borderColor)
                    .append("margin-top", new Unit(-0.5).mult(this.height).value)
                    .append("margin-left", new Unit(-0.5).mult(this.thick).value)
                    .transform("rotateY(-90deg) translateZ(" + new Unit(0.5).mult(this.width).value + ")")
                    .value;
            },
            rightStyle() {
                return new Style().append("width", this.thick)
                    .append("height", this.height)
                    .append("background-color", this.rightBackColor)
                    .append("border", "1px solid " + this.borderColor)
                    .append("margin-top", new Unit(-0.5).mult(this.height).value)
                    .append("margin-left", new Unit(-0.5).mult(this.thick).value)
                    .transform("rotateY(90deg) translateZ(" + new Unit(0.5).mult(this.width).value + ")")
                    .value;
            },
            frontStyle() {
                return new Style().append("width", this.width)
                    .append("height", this.height)
                    .append("background-color", this.frontBackColor)
                    .append("border", "1px solid " + this.borderColor)
                    .append("margin-top", new Unit(-0.5).mult(this.height).value)
                    .append("margin-left", new Unit(-0.5).mult(this.width).value)
                    .transform("translateZ(" + new Unit(0.5).mult(this.thick).value + ")")
                    .value;
            },
            backStyle() {
                return new Style().append("width", this.width)
                    .append("height", this.height)
                    .append("background-color", this.backBackColor)
                    .append("border", "1px solid " + this.borderColor)
                    .append("margin-top", new Unit(-0.5).mult(this.height).value)
                    .append("margin-left", new Unit(-0.5).mult(this.width).value)
                    .transform("rotateY(-180deg) translateZ(" + new Unit(0.5).mult(this.thick).value + ")")
                    .value;
            }
        }
    }
</script>

<style scoped lang="scss">
    .material {
        position: absolute;
        @include transform-style(preserve-3d);
    }
    .material > div {
        position: absolute;
        @include sizing;
    }
</style>
